import React from 'react'

const CountBar = ({ classBase, items }) => (
    <ul className={ `${classBase}__count-bar` }>
        { items.map(({ count, labelTop, labelBottom}) => (
            <li key={ count }>
                <span>{ count }</span>
                <span>
                    <span>{ labelTop }</span>
                    <span>{ labelBottom }</span>
                </span>
            </li>
        )) }
    </ul>
)

export default CountBar
